<template>
    <yd-layout>
        <div class="header" @click="commonNew(`/versionHistory`)">查看版本历史记录</div>
        <div class="first" @click="commonNew('/functionDetail/0')">
            <img v-if="listOne.file!=null" :src="'https://mobileoa.unionpay.com/unionpay/uview/docpic/docFile.jsp?FileName='+listOne.file" class="f_img">
            <div class="title">{{listOne.title}}</div>
            <div class="text" v-html="listOne.abstract"></div>
        </div>
        <div class="list" v-for="(item, index) in list" :key="index" v-if="index>0" @click="commonNew('/functionDetail/'+index)">
            <div class="title" v-text="item.title"></div>
            <div class="dateTime" v-text="formatDate(item.pubTime,'yyyy-MM-dd')"></div>
            <div class="text" v-html="item.abstract"></div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'newFunction',
    data() {
        return {
            list: [],
            listOne: '',
            href: ''
        }
    },
    mounted() {
        // 8月16号上线
        document.setTitle('新功能');
        let vm = this;
        vm.messageList();
        vm.href = window.location.host;
    },
    computed: {
        ...mapGetters({
            userInfo: 'getUserInfo'
        })
    },
    methods: {
        messageList() {
            let vm = this;
            vm.get(vm.conf.api + '/AddressBook/notice/messageList', {
                'token': `${vm.userInfo.ssotoken}L/wp-2q${vm.userInfo.ssotokenCode}`
            }, function (res) {
                console.log(res.messageList);
                vm.list = res.messageList;
                vm.listOne = res.messageList[0];
            });
        },
        commonNew(url) {
            const vm = this;
            if (vm.HOST != 'http://172.18.44.44' && vm.HOST != 'http://localhost:8080') {
                let nUrl = this.HOST + url;
                UPCHAT.M.NAPI.startNewWebView(nUrl, {});
            } else {
                vm.$router.push(url);
            }
        }
    }
}
</script>
<style lang="less" scoped>
.header {
    height: .9rem;
    background-color: #fff;
    font-size: .3rem;
    color: #59a5fd;
    line-height: .9rem;
    text-align: right;
    padding-right: .35rem;
}

.first {
    // height: 5rem;
    // margin: .24rem .13rem;
    margin-bottom: .24rem;
    background-color: #fff;
    // border-radius: 5px;
    padding: 0.1rem .28rem .26rem;
    .f_img {
        height: 2.96rem;
        width: 100%;
    }
    .title {
        font-size: .3rem;
        font-weight: bold;
        margin-top: .4rem;
    }
    .text {
        font-size: .28rem;
        color: #999;
        margin-top: .29rem;
        word-break: break-all;
    }
}

.list {
    // height: 2.78rem;
    background-color: #fff;
    padding: .4rem .28rem;
    margin-bottom: .18rem;
    .title {
        font-size: .3rem;
        font-weight: bold;
    }
    .dateTime {
        font-size: .24rem;
        color: #999;
        margin-top: .2rem;
    }
    .text {
        font-size: .28rem!important;
        color: #999;
        margin-top: .2rem;
        word-break: break-all;
    }
}

.list:last-child {
    margin-bottom: 0rem;
}
</style>


